class Nav extends React.Component {

	constructor(props) {
		super(props)
	}
	
	render () {
		return (
			<nav className="main-nav">
				<div className='wrap'>
					{this.props.links.map((link, i) => {
						return (
							<a href={link.href}>{link.name}</a>
						)
					})}
				</div>
			</nav>
		)
	}

}

export default Nav
